.reason {
  display: flex;
  flex-direction: column;
}

.reason__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 11px;
  align-items: center;
  padding: 50px 12px 38px;
  color: $white;
  background-color: $blue;

  &--second {
    padding: 38px 12px 38px;
  }
}

.reason__img {
  display: block;
  margin: 0 auto;
}

.reason__description {
  margin-top: 4px;
  line-height: 22px;
}

@media (min-width: $tablet-width) {
  .reason {
    flex-direction: row;

    &--second {
      flex-direction: row-reverse;
    }
  }

  .reason__text {
    justify-content: start;
    gap: 12px;
    max-width: 256px;
    padding: 40px 33px 38px;
    margin: 0;

    &--second {
      padding-top: 43px;
      padding-right: 35px;;
    }
  }

  .reason__title--second {
    width: 150px;
  }

  .reason__description {
    margin-top: 11px;
  }

  .reason__img {
    margin: 0;
  }
}

@media (min-width: $desktop-width) {

  .reason__text {
    max-width: 400px;
    padding: 50px 66px 58px;

    &--second {
      padding: 50px 84px 58px;
    }
  }
  .reason__title {
    max-width: 215px;
  }
}
